<template>
     <el-form  :model="searchForm" ref="searchForm"  label-width="80px" >
       <el-row>
         <el-col :span="6">
          <el-form-item label="机构" prop="busOrgUuid">
              <el-select v-model="searchForm.busOrgUuid" placeholder="请选择" @change="getLineList(searchForm.busOrgUuid)">
                <el-option
                  v-for="item in lineOrgs"
                  :key="item.orgUuid"
                  :label="item.orgName"
                  :value="item.orgUuid">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="线路" prop="busLineUuid">
              <el-select v-model="searchForm.busLineUuid" placeholder="请选择" @change="getBusesList(searchForm.busLineUuid)">
                <el-option
                  v-for="item in lines"
                  :key="item.lineUuid"
                  :label="item.lineName"
                  :value="item.lineUuid">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="车牌号" prop="busUuid">
              <el-select v-model="searchForm.busUuid" placeholder="请选择">
                <el-option
                  v-for="item in buses"
                  :key="item.busUuid"
                  :label="item.busPlateNumber"
                  :value="item.busUuid">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
         <!-- <el-form-item label="设备编号" prop="devCode">
            <el-input v-entity="searchForm.devCode" placeholder="请输入设备编号"></el-input>
         </el-form-item> -->
         <el-col :span="6" class="flex-right">
          <el-form-item>
              <el-button type="primary" @click.native="search">查询</el-button>
              <el-button @click="resetForm('searchForm')">重置</el-button>
          </el-form-item>
         </el-col>
        </el-row>
      </el-form>
</template>

<script>
// api
import { getAllOrgList, linesList, busesList } from '@/api/common'
export default {
  name: 'LineManagementSearch',
  data () {
    return {
      lineOrgs: [], // 机构列表
      lines: [], // 线路列表
      buses: [], // 车辆列表
      searchForm: {
        busOrgUuid: '', // 组织机构id
        busLineUuid: '', // 线路id
        busUuid: '' // 车辆id
        // devCode: null

      }
    }
  },
  mounted () {
    getAllOrgList().then(res => {
      this.lineOrgs = res
    })

    this.search()
  },
  methods: {
    // 检索
    search: function () {
      this.$emit('search', Object.assign({}, this.searchForm))
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.$emit('rest', Object.assign({}, this.searchForm))
      this.lines = [] // 清空线路列表
      this.buses = [] // 清空车辆列表
    },
    // 通过组织机构的id和启用，是否删除来获取当前线路
    getLineList (lineOrgUuid) {
      this.searchForm.busLineUuid = ''
      this.searchForm.busUuid = ''
      this.lines = []
      this.buses = []
      const params = {lineOrgUuid: lineOrgUuid, lineIsvalid: '1', lineDropFlag: 0}
      linesList(params).then(res => {
        this.lines = res
      })
    },
    // 通过线路uuid和是否删除来获取当前车辆
    getBusesList (busLineUuid) {
      this.searchForm.busUuid = ''
      this.buses = []
      const params = {busLineUuid: busLineUuid, busDropFlag: 0}
      busesList(params).then(res => {
        this.buses = res
      })
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



